/*
** author profile page.
*/
.editor-header .author-img {
  border-radius: 10px;
  float: left;
  overflow: hidden;
  width: 25%;
}

.editor-header .author-img img {
  width: 100%;
  min-width: 100%;
}

.editor-header .author-name {
  float: right;
  width: 68%;
}

.editor-header .author-name h1 {
  font-size: 30px;
  margin: 0 0 5px 0;
}

.editor-header .author-name h3 {
  margin-top: 0;
}

.editor-header .social-author-icons {
  border-bottom: 2px dotted #d1d1d1;
  border-top: 2px dotted #d1d1d1;
  padding: .55rem 0 .55rem;
}

.editor-header .social-author-icons a {
  margin-left: 7px;
}

.editor-header .social-author-icons img:hover {
  border-radius: 50%;
}

.editor-header .field--name-field-designation + .field--name-field-author-company {
  position: relative;
  padding: 0 10px;
}

.editor-header .field--name-field-designation + .field--name-field-author-company:before {
  content: ",";
  position: absolute;
  left: 0;
}

.editor-header .field--name-field-designation,
.editor-header .field--name-field-author-company {
  display: inline-block;
  width: auto;
}

.author-list .view-header span, .author-list .view-empty span {
  font-family: "OpenSansBold", sans-serif;
  color: #800000;
  display: block;
  border-bottom: 2px solid #545454;
  margin-bottom: 15px;
  font-size: 16px;
  padding-bottom: 5px;
  margin-top: 20px;
}

/*
** alliance-partner.
*/
.alliance_partner {
  background: #efefef;
}

.alliance-partner-block .views-row {
  border: 1px solid #e1e1e1;
}

.alliance-partner-page .views-row {
  border-bottom: 2px dotted #e1e1e1;
  display: inline-block;
  padding-bottom: 10px;
  margin-bottom: 20px;
}

.alliance-partner-page .left-txt {
  float: left;
  margin-right: 1.5em;
  border: 1px solid #d1d1d1;
}

.alliance-partner-page .views-field-body {
  float: left;
  width: 65%;
}

.alliance-partner-page .views-field-body a {
  color: #800000;
}

.alliance-partner-page .view-profile {
  border-top: 1px solid #d1d1d1;
  display: block;
  text-align: center;
  padding: 5px 0;
}

.alliance-partner-page .view-profile a {
  color: #800000;
}

.alliance-partner-page .view-profile:empty {
  display: none;
}

.alliance-partner-page .view-content {
  margin-top: 2em;
}

.alliance-partner-page .anchor {
  display: block;
  height: 115px;
  margin-top: -115px;
  visibility: hidden;
}

.block-alliance-partner {
  padding: 30px 0;
}

.block-alliance-partner h2 {
  border-bottom: none;
}

.block-alliance-partner .view-content .views-row {
  padding: 0;
  background: #fff;
}

