/**
 * @file
 * Styles for Bartik's buttons.
 */
.menu .dropdown-menu {
  display: none;
}

.menu .dropdown-menu > li a {
  white-space: nowrap;
  display: block;
}

.menu .menu-item:hover .dropdown-menu {
  display: block;
  position: absolute;
  z-index: 2;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.ad__section {
  background: #f9f9f9;
  padding-top: 20px;
  padding-bottom: 25px;
  text-align: center;
}

ul.menu {
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flexbox;
  display: flex;
  padding-left: 0;
  margin-bottom: 0;
  margin-left: 0;
}

ul.menu > li {
  padding: 0;
  margin-right: 25px;
  list-style-type: none;
  position: relative;
}

ul.menu .menu-item--expanded {
  list-style-type: none;
  list-style-image: none;
}

ul.menu ul {
  padding-left: 0;
}

ul.menu ul li {
  list-style-type: none;
  list-style-image: none;
}

.menu--hyphen .menu-item {
  position: relative;
}

.menu--hyphen .menu-item:after {
  content: "|";
  position: absolute;
  right: -12px;
  top: -1px;
}

.menu--hyphen__not-first.menu--hyphen .menu-item:after {
  right: -16px;
}

@media all and (max-width: 767px) {
  .menu--hyphen__not-first.menu--hyphen .menu-item:after {
    right: -5px;
  }
}

.menu-small.menu--hyphen .menu-item:after {
  color: #ccc;
}

.menu--hyphen .menu-item:last-child:after {
  content: "";
}

.menu--hyphen__not-first.menu--hyphen .menu-item:first-child {
  margin-right: 15px;
}

.menu--hyphen__not-first.menu--hyphen .menu-item:first-child:after {
  content: '';
}

.menu-small {
  font-size: 12px;
}

.menu-small ul.menu > li {
  margin-right: 20px;
}

.menu--quick-links.menu-small ul.menu > li {
  color: #c32d36;
  list-style-type: square;
  margin-left: 16px;
  text-indent: -6px;
  font-size: 16px;
}

.menu--quick-links.menu-small ul.menu > li:first-child {
  margin-left: 6px;
  list-style-type: none;
}

.menu-bold {
  font-family: "OpenSansSemiBold", sans-serif;
  color: #000;
  margin-top: 10px;
}

/*********** collapsed menu ***********/
.collapse__menu {
  display: none;
  position: relative;
}

@media (max-width: 767px) {
  .collapse__menu {
    background: #292929;
    padding: 20px 15px;
  }
}

.collapse__menu ul.menu {
  -webkit-box-direction: normal;
  -webkit-box-orient: vertical;
  -webkit-flex-direction: column;
  -moz-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
}

