  .collapse__menu ul.menu {
    background: #333;
    box-shadow: 1px 1px 3px #000;
    position: absolute;
    z-index: 1;
    width: 190px;
  }
}

.collapse__menu ul.menu li {
  margin-right: 0;
}

@media (min-width: 768px) {
